import $ from 'jquery';
import pretty from 'pretty';

const TASKS = {
    "cs": {
        //"non_breaking_hyphen": ["@(\\w{1})-(\\w+)@i", "$1&#8209;$2"],
        //"numbers": ["@(\\d) (\\d)@i", "$1&nbsp;$2"],
        //"spaces_in_scales": ["@(\\d) : (\\d)@i", "$1&nbsp;:&nbsp;$2"],
        //"ordered_number": ["@(\\d\\.) ([0-9a-z])@", "$1&nbsp;$2"],
        "prepositions ": ["@($|;| |&nbsp;|\\(|\\n|>)(%keys%) @i", "$1$2&nbsp;", "do|krom|od|u|z|ze|za|proti|naproti|kvli|vi|nad|pod|ped|za|o|pro|mezi|pes|mimo|pi|na|po|v|ve|pod|ped|s|za|mezi|se|si|k|je"],
        //"conjunctions": ["@($|;| | |\\(|\\n|>)(%keys%) @i", "$1$2&nbsp;", "a|i|o|u"],
        //"abbreviations": ["@($|;| | |\\(|\\n|>)(%keys%) @i", "$1$2&nbsp;", "v.|cca.|.|s.|j.|p.|fa|f|fy|kup.|mj.|nap.|p.|p|pop.|p.|pib.|pibl.|sl.|str.|sv.|tj.|tzn.|tzv.|zvl."],
        //"units": ["@(\\d) (%keys%)(^|[;\\.!:]| | |\\?|\\n|\\)|<|\\010|\\013|$)@i", "$1&nbsp;$2$3", "m|m|l|kg|h|C|K|lid|dn|%|mil"]
    }, "en": {
        "non_breaking_hyphen": ["@(\\w{1})-(\\w+)@i", "$1&#8209;$2"],
        "numbers": ["@(\\d) (\\d)@i", "$1&nbsp;$2"],
        "spaces_in_scales": ["@(\\d) : (\\d)@i", "$1&nbsp;:&nbsp;$2"],
        "ordered_number": ["@(\\d\\.) ([0-9a-z])@", "$1&nbsp;$2"],
        "prepositions ": ["@($|;| | |\\(|\\n|>)(%keys%) @i", "$1$2&nbsp;", "aboard|about|above|across|after|against|ahead of|along|amid|amidst|among|around|as|as far as|as of|aside from|at|athwart|atop|barring|because of|before|behind|below|beneath|beside|besides|between|beyond|but|by|by means of|circa|concerning|despite|down|during|except|except for|excluding|far from|following|for|from|in|in accordance with|in addition to|in case of|in front of|in lieu of|in place of|in spite of|including|inside|instead of|into|like|minus|near|next to|notwithstanding|of|off|on|on account of|on behalf of|on top of|onto|opposite|out|out of|outside|over|past|plus|prior to|regarding|regardless of|save|since|than|through|throughout|till|to|toward|towards|under|underneath|unlike|until|up|upon|versus|via|with|with regard to|within|without"],
        "conjunctions": ["@($|;| | |\\(|\\n|>)(%keys%) @i", "$1$2&nbsp;", "a|i|o|u"],
        "article": ["@($|;| | |\\(|\\n|>)(%keys%) @i", "$1$2&nbsp;", "a|an|the"],
        "units": ["@(\\d) (%keys%)(^|[;\\.!:]| | |\\?|\\n|\\)|<|\\010|\\013|$)@i", "$1&nbsp;$2$3", "m|m|l|kg|h|C|K|lid|dn|%|mil"]
    }
};

class Nbsp {

    /**  @var {HTMLTextAreaElement} input */
    input;
    /**  @var {HTMLTextAreaElement} input */
    output;
    /**  @var {HTMLDivElement} input */
    preview;


    constructor(input, output, preview) {
        this.input = input;
        this.output = output;
        this.preview = preview;
        input.addEventListener('change', this.update.bind(this));
        input.addEventListener('keyup', () => {
            clearTimeout(this.timeout);
            this.timeout = setTimeout(this.update.bind(this), 500);
        });
        this.init();
        this.update();
    }

    init() {
        this.input.value = localStorage.getItem('value');
        this.tasks = Object.fromEntries(Object.entries(TASKS).map(([lang, types]) => {
            return [lang, Object.values(types).map(([regex, replacement, keys]) => {
                keys && (regex = regex.replace('%keys%', keys));
                let matches = regex.match(/^@(?<reg>.*)@(?<flags>\w+)?$/);
                if (matches) {
                    let {reg, flags} = matches.groups;
                    return [new RegExp(reg, flags), replacement]
                } else {
                    console.log(matches,regex);
                }
            })];
        }));
        console.log(this.tasks);
    }

    store() {
        localStorage.setItem('value', this.input.value);
    }

    update() {
        this.store();
        this.preview.innerHTML = this.input.value;
        this.apply(this.preview);
        this.output.value = pretty(this.preview.innerHTML);
    }

    apply(el) {
        let walker = document.createTreeWalker(el, NodeFilter.SHOW_TEXT, null);
        /**  @var {Node} node  */
        let node;
        while (node = walker.nextNode()) {
            console.log(node,typeof node,node.prototype,node.nodeValue);
            node.textContent = this.nbsp(node.textContent);
            node.parentNode.replaceChild(document.createElement(),node);
        }
    }

    /** @param {string} text */
    nbsp(text) {
        /**  @var {RegExp} regex  */
        for (let [regex, replacement] of this.tasks.cs) {
            text = text.replace(regex, replacement);
        }
        return text;
    }
}

$(() => {
    let nbsp = new Nbsp(
        document.getElementById('input'),
        document.getElementById('output'),
        document.getElementById('preview')
    );
    $(document).bind('keydown', function(e) {
        if(e.ctrlKey && (e.which === 83)) {
            e.preventDefault();
            nbsp.update();
            return false;
        }
    });
});

